@import url(https://fonts.googleapis.com/css2?family=Chivo:wght@300;400;700&display=swap);
*,*:before,*:after{box-sizing:border-box}body{margin:0;background-color:#F5F5F5}h1,h2,h3{margin:0}.App{display:flex;flex-direction:column;align-items:center;height:100vh;overflow:hidden;font-family:'Chivo', sans-serif;color:black;max-width:100%;font-weight:300}.main{display:grid;grid-template-columns:3fr  7fr;height:85vh;width:100vw;grid-gap:1.5em;padding:1.5em}.dailyInfo{display:grid;height:75%;padding:.8em;grid-gap:1em;grid-auto-rows:1fr;grid-auto-columns:1fr;grid-template-areas:"wind wind wind precip" "sunrise sunset clock clock"}.dailyForecast{background-color:white;border-radius:30px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2)}.sevenDayContainer{height:35%;display:grid;padding:.8em;padding-top:0px;grid-template-columns:repeat(7, 1fr);grid-gap:1em;overflow-x:scroll;overflow-y:hidden}.sevenItem{background-color:white;display:flex;flex-direction:column;border-radius:30px;min-width:80px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2)}.imgContainer{display:flex;align-items:center;justify-content:center;height:40%}.imgContainer img{max-width:40%;height:auto}.dayContainer{height:30%;display:flex;align-items:center;justify-content:center;font-size:25px}.tempContainer{height:30%;display:flex;align-items:center;justify-content:center;font-size:25px}.wind{display:flex;grid-area:wind;align-items:center;justify-content:space-between;background-color:white}.windContainer{display:flex;align-items:center;width:100%;justify-content:center}.windContainer img{height:160px}.windSpeed{display:flex;align-items:center;width:100%;justify-content:center;font-size:5vw}.arrow{font-size:100px;animation:arrow 1s linear}.precip{grid-area:precip;background-color:white}.sunrise{grid-area:sunrise;background-color:white}.sunset{grid-area:sunset;background-color:white}.clock{grid-area:clock;background-color:white}.item{border-radius:30px;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2)}.gridContainer{border-radius:30px;display:flex;flex-direction:column}.sunContainer{display:flex;align-items:center;width:100%;justify-content:center;height:70%}.sunContainer img{max-width:60%;max-height:60%}.sunText{display:flex;align-items:center;justify-content:center;font-size:45px;font-weight:400}.loadContainer{display:flex;align-items:center;justify-content:center;height:92vh;width:100vw;background-color:#F5F5F5;font-size:15vw}.loader{display:flex;align-items:center;justify-content:center;animation:spin 1s linear 0s infinite;transform-origin:center}@keyframes spin{0%{transform:rotate(0deg)}100%{transform:rotate(360deg)}}@keyframes arrow{0%{transform:rotate(0deg)}100%{}}.search{height:8vh;width:90vw;display:flex;align-items:center;justify-content:flex-end;margin-bottom:2vh;border-bottom:1px solid black}.search .searchSubmit{background-color:transparent;border:none;font-size:1.5vw}.search input{background-color:transparent;border:none;font-size:2vw;font-family:'Chivo', sans-serif}.search input:focus{outline:none}@media screen and (max-width: 800px){.search{justify-content:flex-start;width:80vw}.search .searchSubmit{background-color:transparent;border:none;font-size:3.5vw;padding-top:1%}.search input{background-color:transparent;border:none;width:80%;font-size:5vw;font-family:'Chivo', sans-serif}.main{grid-template-columns:1fr;grid-gap:.5em}.sevenDayContainer{height:150px;display:grid;grid-gap:.5em;overflow-x:scroll;overflow-y:hidden;width:90vw;padding-right:1.5em}.sevenItem{min-height:100px;min-width:80px}.dailyForecast{height:25vh}.gridContainer{height:60vh}.tempContainer{font-size:15px}.dayContainer{font-size:15px}.sunText{font-size:25px}.windContainer img{height:80px}.windSpeed{display:flex;align-items:center;width:100%;justify-content:center;font-size:5vw}.arrow{font-size:50px;animation:arrow 1s linear}.clock{display:none}.dailyInfo{display:grid;height:75%;padding:.8em;grid-gap:1em;grid-auto-rows:1fr;grid-auto-columns:1fr;grid-template-areas:"wind wind wind " "sunrise sunset precip"}}

*,*:before,*:after{box-sizing:border-box}html,body,div,span,object,iframe,figure,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,code,em,img,small,strike,strong,sub,sup,tt,b,u,i,ol,ul,li,fieldset,form,label,table,caption,tbody,tfoot,thead,tr,th,td,main,canvas,embed,footer,header,nav,section,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:none;-moz-text-size-adjust:none;text-size-adjust:none}footer,header,nav,section,main{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}input{-webkit-appearance:none;border-radius:0}


